#infinity-outline{fill:transparent;stroke-width:4;stroke:#fff;stroke-dasharray:3px,300px;stroke-dashoffset:0;animation:anim 3000ms linear infinite}#infinity-bg{fill:transparent;stroke-width:4;stroke:#fff;opacity:.2}@keyframes anim{12.5%{stroke-dasharray:42px,300px;stroke-dashoffset:-33px}43.75%{stroke-dasharray:105px,300px;stroke-dashoffset:-105px}100%{stroke-dasharray:3px,300px;stroke-dashoffset:-297px}}
